import createRenderNode from './createRenderNode';
import * as actions from './actions';
import onAction from './handlers/onAction';
import { BiPluginConfig, BiPlugin } from '../../types';
import { moFactory } from '@ali/4ever-plugin-placeholder';

function createPlaceholderPlugin(config: BiPluginConfig): BiPlugin {
  const placeholderConfig = config.placeholder || {};

  return {
    ...moFactory({}),
    renderNode: createRenderNode(placeholderConfig),
    onAction,
    models: [
      {
        klass: 'block',
        type: 'ph',
      },
      {
        klass: 'inline',
        type: 'iph',
      },
    ],
  };
}

createPlaceholderPlugin.actions = actions;

export default createPlaceholderPlugin;
